* {
    margin: 0;
    padding: 0;
    box-sizing: inherit;


    // ? testing
    // border: 1px solid red;
}

html {
    box-sizing: border-box;
    font-size: 62.5%;

    @include respond(media-sm) {
        font-size: 50%;
    }
}

body {
    box-sizing: border-box;
    font-weight: 400;
    font-size: $default-text-size;
    font-family: 'Dosis', sans-serif;
    color: $dirty-white;
    @include fluid('font-size', 18, 28);
    line-height: 1.5;

    // background-color: #29323c;
    position: relative;
}

::-webkit-scrollbar {
    display: none;
}

section {
    @include flexCenter;
    background-image: linear-gradient(to right, #201c2d 0%, #29323c 100%);
    // background-color: #29323c;

}