.contact {
    position: relative;
    box-shadow: 0 1rem 4rem $shadow-2;
    padding: 4rem;
    border-radius: 1rem;
    transition: all .5s;
    text-align: center;

    &__profile {
        padding: 2rem;
        display: inline-block;
        border-radius: 1rem;
        position: relative;
        height: 15rem;
        width: 15rem;
    }

    &__logo {
        @include absCentering;
        z-index: 1;
        height: 8rem;
    }

    &__details {
        margin-bottom: 2rem;
    }

    &__name {}

    &__mail {

        &:link,
        &:visited {
            text-decoration: none;
            color: $text;
            color: $grey-text;
            @include fluid('font-size', 16, 24);
            transition: all .2s;
        }

        &:hover,
        &:active {
            color: $text;
        }
    }

    &__text {
        @include fluid('font-size', 16, 22);
        margin-bottom: 4rem;
    }

    &__social {}

    &__link {

        &:link,
        &:visited {
            text-decoration: none;
        }

        &:not(:last-child) {
            margin-right: 4rem;
        }
    }

    &__img {
        @include fluid('height', 30, 40);
    }
}

.copyright {
    &__text {
        @include fluid('font-size', 14, 20);
    }
}