.menu {
    position: relative;

    &__checkbox {
        display: none;
    }

    &__button {
        background-color: transparent;
        height: 4rem;
        width: 4rem;
        position: fixed;
        @include fluid('top', 30, 60);
        @include fluid('right', 30, 60);
        z-index: 4;
        text-align: center;
        cursor: pointer;
        // border: 1px solid blue;
    }

    &__bg {
        height: 100vh;
        width: 100%;
        // background-color: #29323c;
        background-image: linear-gradient(to right, #201c2d 0%, #29323c 100%);
        position: fixed;
        top: 0;
        left: 0;
        // * testing
        visibility: hidden;
        opacity: 0;
        transform: translateX(-100vw);
        z-index: 3;
        transition: all .7s;
    }

    &__nav {
        height: 100vh;
        width: 100%;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 3;
        transform: translateX(-50vw);
        transition: all .5s;
        // * testing
        opacity: 0;
        visibility: hidden;
    }

    &__list {
        @include absCentering;

        @include respond(media-sm) {
            top: 45%;
        }

        list-style: none;
        text-align: center;
    }

    &__item {
        margin: 1rem;
        position: relative;
        display: flex;
        align-items: center;
        // justify-content: center;
    }

    &__nav-icon {
        @include fluid('height', 20, 30);
        margin-right: 1rem;
        filter: drop-shadow(0 .5rem 2rem $shadow-4);
    }

    &__link {

        &:link,
        &:visited {
            position: relative;
            display: inline-block;
            @include fluid('font-size', 24, 48);
            text-decoration: none;
            padding: 1rem 2rem;
            color: $dirty-white;
            text-transform: uppercase;
            background-image: linear-gradient(120deg,
                    transparent 0%,
                    transparent 50%,
                    #2fc9fe 50%);
            background-size: 240%;
            transition: all .4s;
            border-radius: 5px;
            z-index: 3;
            text-shadow: 0 .5rem 1rem $shadow-6;
            backface-visibility: hidden;
        }

        &:hover,
        &:active {
            background-position: 100%;
            color: #201c2d;
            color: white;
            transform: translateX(2rem);
        }
    }

    &__checkbox:checked~&__bg {
        visibility: visible;
        opacity: 1;
        transform: translateX(0);
    }

    &__checkbox:checked~&__nav {
        transform: translateX(0);
        opacity: 1;
        visibility: visible;
    }

    // ICON
    &__icon {
        position: relative;
        margin-top: 1.5rem;

        &,
        &::before,
        &::after {
            width: 4rem;
            height: 3px;
            background-color: $color-1;
            display: inline-block;
        }

        &::before,
        &::after {
            content: '';
            position: absolute;
            left: 0;
            transition: all .2s;
        }

        &::before {
            top: -1.2rem;
        }

        &::after {
            top: 1.2rem;
        }
    }

    &__button:hover &__icon::before {
        top: -1.4rem;
    }

    &__button:hover &__icon::after {
        top: 1.4rem;
    }

    &__checkbox:checked+&__button &__icon {
        background-color: transparent;
    }

    &__checkbox:checked+&__button &__icon::before {
        top: 0;
        transform: rotate(135deg);
    }

    &__checkbox:checked+&__button &__icon::after {
        top: 0;
        transform: rotate(-135deg);
    }
}

