// TYPOGRAPHY STYLES

.primary-heading {
    font-family: 'Dosis', sans-serif;

    &--top {
        display: block;
        text-shadow: 0 .5rem 1rem $shadow-4;
        @include fluid('font-size', 30, 75);
        @include fluid('margin-bottom', 5, 15);

        &::after {
            content: '';
            display: block;
            width: 35%;
            height: 3px;
            background-color: $color-1;
            box-shadow: 0 .3rem 1rem $shadow-8;

            @include respond(media-sm) {
                width: 15%;
            }
        }
    }

    &--bottom {
        display: block;

        @include fluid('font-size', 18, 40);
        font-weight: 400;

        &--1 {
            @include fluid('font-size', 16, 30);
        }
    }
}