.btn {

    &,
    &:link,
    &:visited {
        text-transform: uppercase;
        text-decoration: none;
        color: $color-1;
        background-color: transparent;
        @include fluid('font-size', 14, 20);
        padding: 1.5rem 2rem;
        border-radius: 3px;
        text-shadow: 0 .5rem 2rem $shadow-6;
        border: 3px solid $color-1;
        font-weight: 600;
        letter-spacing: 1px;
        transition: all .3s;
    }

    &:hover {
        background-color: $color-1;
        color: $dirty-white;
    }
}