$strokeWidth: 4px;
$strokeColor: white;

$timing: 2.6s;
$easing: cubic-bezier(0.77, 0, 0.175, 1);


.scroll-box {
    // border: 1px dashed red;
    display: inline-block;
    text-align: center;
    width: 10rem;
    display: flex;

    @include respond(media-sm) {
        width: 100%;
        justify-content: center;
    }


    // ARROW STYLES
    &__arrow:before,
    &__arrow:after {
        content: '';
        display: block;
        @include fluid('width', 12, 20);
        @include fluid('height', 12, 20);
        transform: rotate(45deg);
        border-bottom: $strokeWidth solid $strokeColor;
        border-right: $strokeWidth solid $strokeColor;
    }

    &__arrow:before {
        animation: down-arrow-before $timing $easing infinite;
    }

    &__arrow:after {
        animation: down-arrow-after $timing $easing infinite;
    }

    // TEXT
    &__text {
        text-transform: uppercase;
        @include fluid('font-size', 14, 20);
        @include fluid('margin-bottom', 12, 20);
        animation: pulse 2s linear alternate infinite;
    }
}