#back-to-top {
    display: none;
    opacity: .3;
    cursor: pointer;
    content: '';
    position: absolute;
    bottom: 2rem;
    right: 2rem;
    height: 35px;
    width: 35px;
    border: 2px solid #fff;
    position: fixed;
    border-radius: 50rem;
    z-index: 3;
    transition: all 200ms ease;
}

#back-to-top:hover {
    opacity: 1;
}

#back-to-top::before {
    content: '';
    position: absolute;
    width: 10px;
    top: 40%;
    left: 6px;
    -webkit-transform: translateY(-50%) rotate(-45deg);
    transform: translateY(-50%) rotate(-45deg);
    -webkit-transform-origin: 100%;
    transform-origin: 100%;
    border-bottom: 2px solid #fff;
}

#back-to-top::after {
    content: '';
    position: absolute;
    width: 10px;
    top: 40%;
    right: 6px;
    -webkit-transform: translateY(-50%) rotate(45deg);
    transform: translateY(-50%) rotate(45deg);
    -webkit-transform-origin: 0%;
    transform-origin: 0%;
    border-bottom: 2px solid #fff;
}