.skills {
    position: absolute;
    height: 100vh;
    width: 100vw;
    background-color: #29323c;
    z-index: 10;
    border-radius: .5rem;
    box-shadow: 0 1rem 3rem $shadow-4;
    background-image: linear-gradient(to right bottom, #201c2d 0%, #29323c 100%);
    // background-color: #29323c;
    opacity: 0;
    visibility: 0;
    padding: 5rem;
    transform: translateX(-90rem);
    transition: all .3s;

    @include respond(media-sm) {
        height: 80vh;
        width: 100vw;
    }

    &:target {
        opacity: 1;
        visibility: visible;
        transform: translateX(0);
    }

    // CLOSE BUTTON
    &__link {
        position: absolute;
        top: 3rem;
        right: 3rem;

        &:link,
        &:visited {
            text-decoration: none;
            color: $dirty-white;
            transition: all .2s;
        }

        &:hover,
        &:active {
            color: orangered;
        }
    }

    &__icon {
        font-size: 4rem;
    }
}