.info-box {
    position: relative;
    box-shadow: 0 1rem 4rem $shadow-2;
    padding: 3rem;
    border-radius: 1rem;
    transition: all .5s;

    &__img {
        @include fluid('height', 100, 200);
        box-shadow: 0 1rem 3rem $shadow-2;
        position: absolute;
        @include fluid('bottom', -70, -150);
        @include fluid('right', 20, 50);
        outline: 5px solid $color-1;
        outline-offset: 1rem;
        transform: rotate(5deg);
        transition: all .4s;
    }

    &__text {
        @include fluid('font-size', 16, 23);
        letter-spacing: 1px;
        text-align: justify;
        padding-bottom: 6rem;
    }
}