.img-comp {
    // @include fluid('margin-top', 0, -80);
    @include flexCenter;
    height: 100%;

    @include respond(media-sm) {
        display: none;
    }

    &__container {
        width: 1rem;
        @include fluid('height', 50, 10);
        position: relative;
    }

    &__img {
        position: absolute;
        filter: drop-shadow(0 1rem 3rem $shadow-2);
        @include fluid('height', 130, 330);
        transition: all .3s;
        outline-offset: 1rem;

        &-p1 {
            @include fluid('top', 0, -170);
            @include fluid('left', -20, 30);
            transform: rotate(35deg);
            // animation: rotation 6s infinite linear;

            // testing
            // border: 3px dashed red;
        }

        &-p2 {
            @include fluid('top', 50, 0);
            @include fluid('left', -50, -125);
            transform: rotate(-15deg);
            z-index: 1;
            // animation: rotation-left 10s infinite linear;

            // testing
            // border: 3px dashed yellow;
        }

        &-p3 {
            @include fluid('top', 10, -150);
            @include fluid('right', -15, -112);
            transform: rotate(15deg);
            // animation: rotation 12s infinite linear;

            // testing
            // border: 3px solid fuchsia;
        }
    }
}